(() => {
  "use strict";

  // Utility functions
  const utils = {
    throttle(fn, wait = 120) {
      let last = 0;
      let timeout = null;
      return (...args) => {
        const now = Date.now();
        const remaining = wait - (now - last);
        if (remaining <= 0) {
          last = now;
          fn(...args);
        } else if (!timeout) {
          timeout = setTimeout(() => {
            timeout = null;
            last = Date.now();
            fn(...args);
          }, remaining);
        }
      };
    },

    sanitize(str = "") {
      return String(str).replace(/[&<>"']/g, (ch) => {
        const map = {
          "&": "&amp;",
          "<": "&lt;",
          ">": "&gt;",
          '"': "&quot;",
          "'": "&#39;",
        };
        return map[ch];
      });
    },

    toNumber(str = "0") {
      return parseFloat(String(str).replace(/[^0-9.]/g, "")) || 0;
    },
  };

  // Main app object
  const app = {
    io: null,
    dom: {},

    cacheDom() {
      this.dom = {
        backToTop: document.getElementById("backToTop"),
        hamburger: document.getElementById("hamburger"),
        mobileMenu: document.getElementById("mobile-menu"),
        progressBar: document.querySelector("#hero-meter-bar > div, .progress-bar"),
        progressPercent: document.getElementById("hero-meter-percent"),
        emojiMilestone: document.getElementById("emoji-milestone"),
        fundsRaised: document.getElementById("funds-raised") || document.getElementById("funds-raised-meter"),
        fundsGoal: document.getElementById("funds-goal") || document.getElementById("funds-goal-meter"),
        vipToast: document.getElementById("vip-toast"),
        sponsorModal: document.getElementById("sponsor-spotlight-modal") || document.getElementById("sponsor-spotlight-modal-footer"),
        sponsorNameEl: document.getElementById("sponsor-name") || document.getElementById("sponsor-name-footer"),
        amountEl: document.getElementById("donation-amount") || document.getElementById("hero-donation-amount"),
        impactEl: document.getElementById("impact-message") || document.getElementById("hero-impact-message"),
        heroHeading: document.getElementById("hero-heading"),
        donationTicker: null,
        sponsorLeaderboard: document.getElementById("sponsor-leaderboard-main"),
      };
    },

    init() {
      this.cacheDom();
      this.setupHeroHeading();
      this.setupSocket();
      this.bindEvents();
      this.setupScrollAnimations();
      this.updateProgressMeter();
      this.setupVIPToast();
      this.setupHeroQuotes();
      this.setupLiveImpactPreview();
    },

    setupHeroHeading() {
      const heading = this.dom.heroHeading;
      if (heading) {
        heading.style.opacity = "1";
        heading.style.visibility = "visible";
        heading.style.transition = "none";
        heading.style.animation = "none";
        heading.style.color = "#facc15";
        heading.style.textShadow = "0 0 8px #facc15cc, 0 4px 12px rgba(250, 204, 21, 0.5)";
      }
    },

    setupSocket() {
      try {
        if (!window.io) {
          console.warn("⚠️ Socket.IO not available – real-time features disabled.");
          return;
        }
        this.io = window.io();
        this.io.on("new_donation", this.handleNewDonation.bind(this));
        this.io.on("new_sponsor", this.handleNewSponsor.bind(this));
      } catch {
        console.warn("⚠️ Socket.IO not available – real-time features disabled.");
      }
    },

    handleNewDonation(data) {
      if (!data) return;
      const amount = Number(data.amount || 0);
      const name = utils.sanitize(String(data.name || "Anonymous"));
      this.showDonationTicker(`🎉 <b>$${amount.toLocaleString()}</b> from <b>${name}</b> – Thank you!`);
      (window.launchConfetti || this.launchConfetti).call(this);
      this.updateProgressMeter();
    },

    handleNewSponsor(data) {
      if (!data) return;
      const name = utils.sanitize(String(data.name || "A Generous Donor"));
      this.sponsorAlert(name, "Champion Sponsor");
      this.openSpotlight(name);
    },

    bindEvents() {
      const { backToTop, hamburger, heroHeading } = this.dom;

      if (backToTop) backToTop.addEventListener("click", this.scrollToTop.bind(this));
      if (hamburger) hamburger.addEventListener("click", this.toggleMobileNav.bind(this));

      document.addEventListener("click", (e) => {
        const quoteBtn = e.target.closest(".copy-quote");
        if (quoteBtn) this.copyQuote(quoteBtn);
      });

      window.addEventListener("scroll", utils.throttle(this.revealHeadings.bind(this), 120), { passive: true });
      this.revealHeadings();

      if (heroHeading) heroHeading.addEventListener("focus", this.setupHeroHeading.bind(this));
    },

    scrollToTop() {
      window.scrollTo({ top: 0, behavior: "smooth" });
    },

    toggleMobileNav() {
      const { mobileMenu, hamburger } = this.dom;
      if (!mobileMenu || !hamburger) return;

      const isOpen = mobileMenu.classList.toggle("open");
      hamburger.setAttribute("aria-expanded", String(isOpen));
      document.body.style.overflow = isOpen ? "hidden" : "";

      if (isOpen) {
        mobileMenu.querySelector('a,button,input,select,textarea,[tabindex]:not([tabindex="-1"])')?.focus();
      }
    },

    copyQuote(button) {
      const text = button.dataset.quote || "";
      if (!navigator.clipboard) {
        alert("Copy not supported on this browser.");
        return;
      }
      navigator.clipboard.writeText(text).then(() => {
        const container = button.closest("figure,div,section")?.querySelector("#quote-status");
        if (container) container.textContent = "Quote copied to clipboard";

        const originalText = button.textContent;
        button.textContent = "Copied!";
        button.classList.add("bg-yellow-300", "text-black", "font-bold");

        setTimeout(() => {
          if (container) container.textContent = "";
          button.textContent = originalText;
          button.classList.remove("bg-yellow-300", "text-black", "font-bold");
        }, 1400);
      }).catch(() => alert("Copy failed"));
    },

    revealHeadings() {
      document.querySelectorAll("h1, h2").forEach((el) => {
        if (!el.classList.contains("in-view") && el.getBoundingClientRect().top < window.innerHeight - 60) {
          el.style.opacity = "1";
          el.classList.add("in-view");
        }
      });
    },

    setupScrollAnimations() {
      if (!("IntersectionObserver" in window)) return;

      const observer = new IntersectionObserver((entries, obs) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.classList.add("in-view", "animate-sparkle");
            obs.unobserve(entry.target);
          }
        });
      }, { threshold: 0.4 });

      document.querySelectorAll(".badge-glass, .prestige-badge").forEach((el) => observer.observe(el));
    },

    updateProgressMeter() {
      const { progressBar, progressPercent, emojiMilestone, fundsRaised, fundsGoal } = this.dom;
      if (!progressBar || !fundsRaised || !fundsGoal) return;

      const raisedNum = utils.toNumber(fundsRaised.textContent);
      const goalNum = Math.max(utils.toNumber(fundsGoal.textContent), 1);
      const pct = Math.min((raisedNum / goalNum) * 100, 100).toFixed(1);

      setTimeout(() => {
        progressBar.style.width = `${pct}%`;
        progressBar.setAttribute("aria-valuenow", String(raisedNum));
        progressBar.setAttribute("aria-valuemin", "0");
        progressBar.setAttribute("aria-valuemax", String(goalNum));

        if (progressPercent) progressPercent.textContent = `${pct}%`;

        if (emojiMilestone) {
          emojiMilestone.textContent =
            pct >= 100 ? "🏆" :
            pct >= 75 ? "🚀" :
            pct >= 50 ? "🔥" :
            pct >= 25 ? "🚀" : "💤";
        }
      }, 300);
    },

    setupVIPToast() {
      const { vipToast } = this.dom;
      if (!vipToast) return;

      if (!sessionStorage.getItem("vipToastShown")) {
        vipToast.textContent = "🎉 Welcome! New sponsors will be spotlighted here — you could be next!";
        vipToast.classList.remove("hidden");
        vipToast.setAttribute("role", "status");
        vipToast.setAttribute("aria-live", "polite");
        setTimeout(() => vipToast.classList.add("hidden"), 6500);
        sessionStorage.setItem("vipToastShown", "1");
      }
    },

    setupHeroQuotes() {
      if (!Array.isArray(window.heroQuotes) || window.heroQuotes.length === 0) return;
      const { text, author, title, avatar } = window.heroQuotes[0];
      const textEl = document.getElementById("hero-quote-text");
      if (textEl) textEl.innerText = text || "";
      const authorEl = document.getElementById("hero-quote-author");
      if (authorEl) authorEl.innerText = author || "";
      const titleEl = document.getElementById("hero-quote-title");
      if (titleEl) titleEl.innerText = title || "";
      if (avatar) {
        const avatarEl = document.getElementById("hero-quote-avatar");
        if (avatarEl) avatarEl.setAttribute("src", avatar);
      }
    },

    setupLiveImpactPreview() {
      const { amountEl, impactEl } = this.dom;
      if (!amountEl || !impactEl) return;

      impactEl.setAttribute("role", "status");
      impactEl.setAttribute("aria-live", "polite");

      const updateImpact = () => {
        const val = parseFloat(amountEl.value) || 0;
        let msg = "";

        if (val >= 150) {
          msg = `🎓 Your $${val} → one week of gym time for all players!`;
        } else if (val >= 100) {
          msg = `🏀 Your $${val} → a full scholarship for a player!`;
        } else if (val >= 50) {
          msg = `👕 Your $${val} → a new team jersey.`;
        } else {
          msg = `👏 Every dollar counts — thank you!`;
        }

        impactEl.textContent = msg;
        impactEl.classList.remove("hidden");
      };

      updateImpact();
      amountEl.addEventListener("input", updateImpact, { passive: true });
    },

    showDonationTicker(message) {
      let ticker = this.dom.donationTicker;
      if (!ticker) {
        ticker = document.createElement("div");
        ticker.id = "donation-ticker";
        ticker.className = "fixed bottom-8 left-1/2 -translate-x-1/2 bg-yellow-400/95 text-black font-bold px-7 py-3 rounded-2xl shadow-xl z-[9999] text-lg animate-bounce-in";
        ticker.setAttribute("role", "status");
        ticker.setAttribute("aria-live", "polite");
        document.body.appendChild(ticker);
        this.dom.donationTicker = ticker;
      }
      ticker.innerHTML = message;
      ticker.classList.add("show");
      setTimeout(() => ticker.classList.remove("show"), 4000);
    },

    openSpotlight(name = "A Generous Donor") {
      const { sponsorModal, sponsorNameEl } = this.dom;
      if (!sponsorModal || !sponsorNameEl) return;

      sponsorNameEl.innerHTML = `<span class="text-red-400 font-bold">${utils.sanitize(name)}</span>`;
      sponsorModal.classList.add("show");
      sponsorModal.setAttribute("aria-modal", "true");

      (window.launchConfetti || this.launchConfetti).call(this);

      setTimeout(() => this.closeSpotlight(), 4000);
    },

    closeSpotlight() {
      const { sponsorModal } = this.dom;
      if (sponsorModal) sponsorModal.classList.remove("show");
    },

    renderSponsorLeaderboard(sponsors = []) {
      const container = this.dom.sponsorLeaderboard;
      if (!container) return;

      if (sponsors.length === 0) {
        container.innerHTML = '<div class="col-span-2 text-center text-lg text-gold/80 font-semibold">Be our first sponsor! 🏆</div>';
        return;
      }

      container.innerHTML = sponsors
        .map((sponsor, index) => {
          const isTop = index === 0;
          return `
            <div class="rounded-2xl border-4 ${isTop ? "border-gold bg-gradient-to-r from-gold/20 via-red-700/10 to-white/10 scale-105 shadow-inner-gold animate-pulse" : "border-white/20 bg-black/40"} shadow-elevated p-5 flex flex-col items-center text-center">
              <span class="text-2xl font-extrabold ${isTop ? "text-gold drop-shadow" : "text-white/80"}">${utils.sanitize(sponsor.name)}</span>
              <span class="text-xl font-bold mt-2 ${isTop ? "text-red-400" : "text-white/60"}">$${Number(sponsor.amount || 0).toLocaleString()}</span>
              ${isTop ? '<div class="prestige-badge mt-3">🏆 Top Champion</div>' : ""}
            </div>
          `;
        })
        .join("");
    },

    sponsorAlert(name, tier = "Champion Sponsor") {
      document.querySelectorAll(".starforge-sponsor-alert")?.forEach((el) => el.remove());

      const alert = document.createElement("div");
      alert.className = "starforge-sponsor-alert fixed bottom-4 right-4 z-[9999] flex flex-col items-end animate-bounce-in";
      alert.setAttribute("role", "status");
      alert.setAttribute("aria-live", "polite");

      alert.innerHTML = `
        <div class="bg-black border-4 border-gold rounded-2xl px-7 py-4 shadow-elevated flex flex-col items-center text-center">
          <img src="/static/images/logo.webp" class="w-12 h-12 mb-2 rounded-full border-2 border-gold shadow-gold-glow" alt="Logo" />
          <span class="text-lg font-bold text-gold mb-2">🔥 NEW SPONSOR ALERT! 🔥</span>
          <span class="text-white font-semibold mb-2">Thank you <b>${utils.sanitize(name)}</b> for supporting the team!</span>
          <span class="prestige-badge">${utils.sanitize(tier)} 🏆</span>
        </div>
      `;

      document.body.appendChild(alert);
      setTimeout(() => alert.remove(), 4800);
    },

    launchConfetti() {
      const colors = ["#facc15", "#dc2626", "#fff", "#18181b"];
      for (let i = 0; i < 64; i++) {
        const confetti = document.createElement("div");
        confetti.className = "confetti";
        confetti.style.background = colors[Math.floor(Math.random() * colors.length)];
        confetti.style.left = `${Math.random() * 100}vw`;
        confetti.style.animationDelay = `${Math.random() * 1.5}s`;
        document.body.appendChild(confetti);
        setTimeout(() => confetti.remove(), 1800);
      }
      if (navigator.vibrate) navigator.vibrate([22, 16, 6]);
    },
  };

  // Initialize when DOM is ready
  document.addEventListener("DOMContentLoaded", () => app.init());

  // Expose some methods globally if needed
  window.FC = {
    showDonationTicker: app.showDonationTicker.bind(app),
    openSpotlight: app.openSpotlight.bind(app),
    closeSpotlight: app.closeSpotlight.bind(app),
    renderSponsorLeaderboard: app.renderSponsorLeaderboard.bind(app),
    sponsorAlert: app.sponsorAlert.bind(app),
    launchConfetti: app.launchConfetti.bind(app),
  };
})();

